.button {
  padding: 13px 20px;
  border: none;
  border-radius: 2px; }
  .button--primary {
    color: #fff;
    background-color: #E3B873; }

.container {
  max-width: 1256px;
  margin: auto; }

.header {
  padding: 24px 0px;
  background-color: #131313;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999; }

.menu {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.logo {
  color: #E3B873;
  text-transform: uppercase; }

.nav__item {
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
  margin-right: 40px; }

.phone {
  color: #E3B873; }

.contacts__phone {
  margin-right: 45px; }

.hero {
  background: #131313 url("../img/hero-bg.jpg") no-repeat center/cover;
  height: 100vh;
  color: #fff;
  position: relative;
  padding-top: 100px; }
  .hero__instagram {
    max-width: 80px;
    position: absolute;
    left: 0;
    top: 0;
    height: 100%; }

.instagram {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #131313; }
  .instagram__link {
    text-transform: uppercase;
    color: #E3B873;
    text-decoration: none;
    transform: rotate(90deg); }

.title {
  margin: 0; }

*, *:before, *:after {
  box-sizing: border-box; }

body {
  font-family: 'Futura Book' sans-serif;
  font-size: 14px; }
