.hero {
    background: $dark url('../img/hero-bg.jpg') no-repeat center / cover;
    height: 100vh;
    color: $light;
    position: relative;
    padding-top: 100px;
    &__instagram {
        max-width: 80px;
        position: absolute;
        left: 0;
        top: 0;
        height: 100%; } }
.instagram {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $dark;
    &__link {
        text-transform: uppercase;
        color: $primary;
        text-decoration: none;
        transform: rotate(90deg); } }
.title {
    margin: 0; }
