.header {
    padding: 24px 0px;
    background-color: $dark;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 999; }
.menu {
    display: flex;
    justify-content: space-between;
    align-items: center; }
.logo {
    color: $primary;
    text-transform: uppercase; }
.nav {
    &__item {
        text-decoration: none;
        color: $light;
        text-transform: uppercase;
        margin-right: 40px; } }
.phone {
    color: $primary; }

.contacts {
    &__phone {
        margin-right: 45px; } }
